// src/pages/Wishlist.jsx
import React, { useMemo } from "react";
import { useCart } from "../context/CartContext";
import { streetFoods } from "../services/productService";
import "./wishlist.css";

export default function Wishlist() {
  const {
    wishlist = [],                 // may be [1,2,3] or [{id:1, ...}, ...]
    removeFromWishlist,            // optional
    toggleWishlist,                // fallback remover if provided
  } = useCart();

  // Normalize wishlist to full product objects
  const wishlistItems = useMemo(() => {
    if (!Array.isArray(wishlist)) return [];
    return wishlist
      .map((w) => {
        // already an object with fields?
        if (w && typeof w === "object") return w;
        // else assume it's an ID -> resolve from catalog
        const p = streetFoods.find((x) => x.id === w);
        return p || null;
      })
      .filter(Boolean);
  }, [wishlist]);

  const handleRemove = (id) => {
    if (typeof removeFromWishlist === "function") {
      removeFromWishlist(id);
    } else if (typeof toggleWishlist === "function") {
      toggleWishlist(id); // toggle off
    } else {
      console.warn("No remove handler available in CartContext");
    }
  };

  return (
    <div className="wishlist-page">
      <h1 className="wishlist-title">My Wishlist</h1>

      {wishlistItems.length === 0 && (
        <p className="wishlist-empty">Your wishlist is empty.</p>
      )}

      <div className="wishlist-grid">
        {wishlistItems.map((item) => (
          <div key={item.id} className="wishlist-card">
            <img src={item.image} alt={item.name} className="wishlist-image" />
            <div className="wishlist-info">
              <h2 className="wishlist-name">{item.name}</h2>
              <p className="wishlist-price">â‚¹{item.price}</p>
              <div className="wishlist-actions">
  <button
    className="wishlist-btn wishlist-remove-btn"
    onClick={() => handleRemove(item.id)}
  >
    Remove
  </button>
  <a
    href={`/product/${item.id}`}
    className="wishlist-btn wishlist-view-btn"
  >
    View
  </a>
</div>

            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
